@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');
:root {
  --primary-white: #ffffff;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-orange: #f9ab00;
  --primary-dark: rgb(52, 58, 64);/*2.to fix drop element when click, cause by adding border top active styling*/
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-black: #000000;
  --primary-transparent-black: rgba(0,0,0,0.7);
  --primary-green: green;
  }
body {
  font-family: 'Roboto Condensed', sans-serif !important;
}
/********************
        NavBar
*********************/
nav {
  z-index: 2;
}
.logo {
  width: 16.25rem;
}
nav a.nav-link {
  letter-spacing: 0.0375rem;/*0.6px*/
  cursor: pointer;
}
nav ul li {
  text-transform: uppercase;
}
.navbar-nav > li > a {
  margin-right: .75rem;
  margin-left: .75rem;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid var(--primary-dark);
}
.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}
@media(max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}
.navbar-nav > li > a {
  border-bottom: 0.1rem solid transparent;/*to fix jump*/
}
.navbar-nav > li > a.active ,  .navbar-nav > li > a:focus {/*1.active is BS class*/
 /* .navbar-nav > li > a.active, .navbar-nav > li > a:focus {/*a.active:focus when design first, change at the end*/
  border-bottom: 0.1rem solid var(--primary-orange);
}
.navbar-nav > li > a:hover {
  border-bottom: 0.1rem solid var(--primary-orange);
}
/********************
        Header/hero
*********************/
.header-wrapper {
  position: relative;
  background: url(wall-and-laptop-background.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}
.main-info {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: 'Roboto Condensed', sans-serif;
  z-index: 1;
}
.main-info h1 {
  color: var(--primary-orange);
  text-transform: uppercase;
}
.typed-text {
  font-size: 2rem;
  color: var(--primary-white);
}
@media(max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}

.btn-main-offer {
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  /*border-radius: 0;*/
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: var(--primary-white);
  margin: 2rem;
}
.btn-main-offer:hover {
  text-decoration: none;
  background-color: var(--primary-hover-red);
  color: var(--primary-white);
  transition: .2s ease-in-out;
}

.tsparticles-canvas-el {/*see CDT for classname*/
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}
@media(max-width: 768px) {
  .particle-canvas {
    display: none;
  }
}
/********************
        About Me
*********************/
.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}
.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: .3rem;
  border: 1px solid var(--primary-orange);
}
.about-heading {
  color: var(--primary-orange);
  text-transform: uppercase;
}
@media(max-width: 468px){
  .profile-img {
    width: 10rem;
  }
}
/********************
        Services
*********************/
.services {
  text-align: center;
  padding-bottom: 2rem;
  background: var(--primary-light-grey);
}
.services h1 {
  color: var(--primary-dark);
  text-transform: uppercase;
}
.services .circle {
  position: relative;
  background:  var(--primary-orange);
  width: 3.125rem;
  height: 3.125rem;
  border-radius: 50%;
  margin: 0.375rem auto;
}
.services .icon {/*can use flex in.circle instead*/
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.4rem;
  color: var(--primary-white);
}
.services .box {
  height: 16rem;
  border-bottom: 0.3125rem solid var(--primary-orange);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: var(--primary-white);
  transition: .3s ease-in-out;
}
.services .box:hover {
  background: var(--primary-orange);
  border-bottom: 0.3125rem solid var(--primary-dark);
}
.services .box:hover .icon {
  color: var(--primary-dark);
}

.services .box:hover .circle {
  background: var(--primary-white);
}

.services .box:hover p {
  color: var(--primary-white);
}
/********************
        Experience
*********************/
.experience {
  padding: 0.7rem;
}
.experience h1 {
  text-transform: uppercase;
  color: var(--primary-orange);
}
.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}
.experience-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: var(--primary-grey);
  /*z-index: 1;*/
}
.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}
.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;/*position the dots*/
}
.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;;
  border: 0.3125rem solid var(--primary-dark);
  background: var(--primary-orange);
  margin-top: 0.625rem;
  z-index: 3;
}
.timeline-content {
  width: 95%;/*to round marker with space*/
  padding: 0 0.9375rem;
  color: var(--primary-dark-grey);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: var(--primary-red);
  font-weight: 900;
}
@media(max-width: 768px) {
  .experience .container {
    padding-left: 0;
  }
  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }
  .timeline-block-left {
    float: none;
    direction: ltr; 
    width: 100%;
  }
}
/********************
        Portfolio
*********************/
.portfolio-wrapper {
  background: var(--primary-light-grey);
  padding: 3rem 0;
}
.portfolio-wrapper h1 {
  color: var(--primary-orange);
}
.portfolio-image {
  width: 15rem;
  border: 1px solid var(--primary-orange);
  padding: 0 0.5rem;
}
.portfolio-image-box {
  position: relative;
  margin: 1rem;
}
.overflow {
  position: absolute;
   opacity: 0;
   background: var(--primary-black);
   width: 15rem;
   height: 8rem;
   top: 0;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}
.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}
.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: var(--primary-orange);
  font-size: 1.5rem;
}
.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}
.popupbox-wrapper {
  margin-top: 7rem;
}
.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
}
.hyper-link {
  cursor: pointer; 
  color: var(--primary-orange);
}
.hyper-link:hover {
  color: var(--primary-red);
}

@media(max-width: 768px){
  .portfolio-image-popupbox {
    width: 100%;
  }
}
@media(max-height: 570px) {
  .popupbox-wrapper {
    height: 100%;
  }
  .portfolio-image-popupbox {
    width: 50%;
  }
  }
  @media(max-height: 280px) {
  .popupbox-wrapper {
    height: 90%;
  }
    .portfolio-image-popupbox {
      width: 30%;
    }
  }
  /********************
        Testimonials
*********************/
.testimonials {
  width: 100%;
  background-image: url(./testimonials-bg.jpg);
  background-size: cover;
  background-color: var(--primary-black);
  text-align: center;
  padding-bottom: 4rem;
  margin-bottom: 0;
}
.testimonials h1 {
  color: var(--primary-orange);
  padding: 3rem 0;
  text-transform: uppercase;
}
.testimonial-content {
  text-align: center;
  max-width: 53.125rem;
  margin: 0 auto 2rem auto;
  background: var(--primary-transparent-black); 
}
.myCarousel {
  color: var(--primary-white);
  background: transparent;
  margin-top: -6%;
  width: 54%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  height: 17.875rem;
}
.myCarousel h3 {
  color: var(--primary-orange);
  font-weight: 600;
  text-transform: uppercase;
}

.myCarousel p {
  color: var(--primary-grey);
}
/*slide &carousel root class see in CDT*/
.carousel .slide img {
  width: 10rem !important;
  border-radius: 50%;
}

.carousel .slide {
  background: transparent !important;
  height: 100% !important;
  padding-top: 2rem;
}

.carousel-root {
  margin: auto !important;
  margin-top: 3% !important;
}
/********************
        Contact
*********************/
.contact {
  background: var(--primary-dark);
  padding: 3rem 0;
}
.contact h1 {
  color: var(--primary-orange);
  padding: 1.3rem 0;
  text-transform: uppercase;
}
.contact p {
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}
.contact input, .contact textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: var(--primary-orange)!important;
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}
.contact .container {
  padding: 4rem 0;
}
.contact textarea {
  height: 12.2rem !important;
}
.contact input:focus, .contact textarea:focus {
  background: transparent;
  color: var(--primary-orange);
  border-bottom: 0.125rem solid var(--primary-dark-grey);
}
.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}
.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: var(--primary-orange);
  display: inline-block;/*to show under input box*/
}
input:focus+.line, textarea:focus+.line { /*to draw line*/
  width: 100%;
  transition: width 0.5s ease-in-out;
}
input.form-control {/*to align with focusline*/
  margin: -0.3rem 0;
}
textarea.form-control {/*to align with focusline*/
  margin: -0.3rem 0;
}
.error-message {
  color: var(--primary-white);
  background: var(--primary-hover-red);
  text-align: left;
}
.success-message {
  font-weight: 900;
  color: var(--primary-green);
}

@media(max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }
  .contacts .container {
    padding: 0 1rem;
  }
  .contacts p {
    padding: 0 0.4rem;
  }
}
/********************
        Footer
*********************/
.footer {
  background: var(--primary-black);
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  text-decoration: none;
  color: var(--primary-orange);
  cursor:pointer;
}

.footer p {
  margin-bottom: 0;
}